/**
 * 计算指定时间距离今天的天数
 * @param timeStamp 输入的时间戳
 * @returns 距离今天的天数
 */
export function calculateDiffDays(timeStamp: number) {
    const nowTime = new Date().getTime();
    const diffTime = Math.abs(nowTime - timeStamp);
    const diffDay = diffTime / 1000 / 60 / 60 / 24;
    if (nowTime > diffTime) {
        return Math.floor(diffDay);
    } else {
        return Math.floor(-diffDay) + 1;
    }
}
